.Settings {
  text-align: center;
}

.Settings-dropdowns {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.Settings-dropdown {
  margin: 5px 10px;
}

.Settings-button {
  background-color: #ccc;
  background-image: -webkit-gradient(
    linear,
    left bottom, right top,
    from(hsl(191, 65%, 40%)),
    to(hsl(191, 65%, 60%))
  );
  background-image: -webkit-linear-gradient(
    left bottom,
    hsl(191, 65%, 40%),
    hsl(191, 65%, 60%)
  );
  background-image: -o-linear-gradient(
    left bottom,
    hsl(191, 65%, 40%),
    hsl(191, 65%, 60%)
  );
  background-image: linear-gradient(
    to right top,
    hsl(191, 65%, 40%),
    hsl(191, 65%, 60%)
  );
  border-radius: 5px;
  border-style: none;
  color: white;
  cursor: pointer;
  font-size: 1em;
  padding: 10px 10px;
  margin: 2px 5px;
}



