.Cell {
  cursor: pointer;
  display: inline-block;
  height: 50px;
  margin: 0 5px;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  position: relative;
  width: 50px;
}

.Cell-flipper {
  height: 50px;
  left: 0;
  position: absolute;
  top: 0;
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: 300ms;
  -o-transition: 300ms;
  transition: 300ms;
  width: 50px;
}

.Cell-panel {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.Cell-flip-enter {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.Cell-flip-enter.Cell-flip-enter-active {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}

.Cell-flip-leave {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}

.Cell-flip-leave.Cell-flip-leave-active {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.Cell-blue {
  background-image: -webkit-gradient(
    linear,
    left bottom, right top,
    from(hsl(191, 65%, 50%)),
    to(hsl(191, 65%, 75%))
  );
  background-image: -webkit-linear-gradient(
    left bottom,
    hsl(191, 65%, 50%),
    hsl(191, 65%, 75%)
  );
  background-image: -o-linear-gradient(
    left bottom,
    hsl(191, 65%, 50%),
    hsl(191, 65%, 75%)
  );
  background-image: linear-gradient(
    to right top,
    hsl(191, 65%, 50%),
    hsl(191, 65%, 75%)
  );
}

.Cell-green {
  background-image: -webkit-gradient(
    linear,
    left bottom, right top,
    from(hsl(132, 55%, 50%)),
    to(hsl(132, 55%, 75%))
  );
  background-image: -webkit-linear-gradient(
    left bottom,
    hsl(132, 55%, 50%),
    hsl(132, 55%, 75%)
  );
  background-image: -o-linear-gradient(
    left bottom,
    hsl(132, 55%, 50%),
    hsl(132, 55%, 75%)
  );
  background-image: linear-gradient(
    to right top,
    hsl(132, 55%, 50%),
    hsl(132, 55%, 75%)
  );
}

.Cell-red {
  background-image: -webkit-gradient(
    linear,
    left bottom, right top,
    from(hsl(0, 65%, 50%)),
    to(hsl(0, 65%, 75%))
  );
  background-image: -webkit-linear-gradient(
    left bottom,
    hsl(0, 65%, 50%),
    hsl(0, 65%, 75%)
  );
  background-image: -o-linear-gradient(
    left bottom,
    hsl(0, 65%, 50%),
    hsl(0, 65%, 75%)
  );
  background-image: linear-gradient(
    to right top,
    hsl(0, 65%, 50%),
    hsl(0, 65%, 75%)
  );
}

/* Stolen from https://codepen.io/mindstorm/pen/GqGVpr */
.Cell-gold {
  background-image: -webkit-gradient(
    linear,
    left bottom, right top,
    color-stop(0, #efd100),
    color-stop(38%, #e2a233),
    color-stop(60%, #f0c328),
    color-stop(86%, #fff1a3),
    to(#ffe13e)
  );
  background-image: -webkit-linear-gradient(
    left bottom,
    #efd100 0,
    #e2a233 38%,
    #f0c328 60%,
    #fff1a3 86%,
    #ffe13e 100%
  );
  background-image: -o-linear-gradient(
    left bottom,
    #efd100 0,
    #e2a233 38%,
    #f0c328 60%,
    #fff1a3 86%,
    #ffe13e 100%
  );
  background-image: linear-gradient(
    to right top,
    #efd100 0,
    #e2a233 38%,
    #f0c328 60%,
    #fff1a3 86%,
    #ffe13e 100%
  );
}



